import * as React from "react"
import { Component } from "react"
import * as ReactDisqusThread from "react-disqus-thread"
import styled from "styled-components"
import PostHeader from "../components/postHeader/postHeader"
import Image from "../components/image/image"
import { ThemeProvider } from "styled-components"
import { theme, GatsbyLink, Title, CasualText } from "../theme"
import "./post.css"
import SEO from "../components/seo/seo"

const BlogPost = styled.div`

  word-wrap: break-word;
  overflow-wrap: break-word;

  padding-right: ${props => props.theme.grid.paddingRight};

  @media (max-width: ${props => props.theme.screen.px1000}) {
    padding-left: ${props => props.theme.grid.paddingLeft};
  }
`

const ImageStyled = styled(Image)`
  margin-bottom: 15%;
`

export default class Post extends React.Component {
  constructor({ data }) {
    super({ data })
    const metaData = data.site.siteMetadata
    const post = data.markdownRemark.frontmatter
    const page = {
      titleAlt: "Article for Scala blog VictoriaZ: " + post.title,
      url: metaData.siteUrl + post.path,
      title: "Article - " + post.title,
      image: metaData.siteLogo,
      main: false,
      description: data.markdownRemark.shortExcerpt,
      keywords: post.tags,
    }

    const article = {
      title: post.title,
      url: metaData.siteUrl + post.path,
      imgUrl: post.featuredImage.childImageSharp.sizes.src,
      imgWidth: 400,
      imgHeight: 300,
      date: post.date,
      tags: post.tags,
      description: data.markdownRemark.longExcerpt,
    }

    this.state = {
      disqus: metaData.disqusShortname,
      post: data.markdownRemark,
      shareUrl: metaData.siteUrlShort + post.path,
      title: post.title,
      page: page,
      article: article,
    }
  }
  componentDidMount = () => {
    this.mountFacebook()
    this.mountReddit()
    this.mountTwitter()
    this.mountAddThis()
    this.mountLikes()
  }

  mountTwitter = () => {
    const script = document.createElement("script")
    script.src = "https://platform.twitter.com/widgets.js"
    script.async = true
    document.body.appendChild(script)
  }

  mountReddit = () => {
    const script = document.createElement("script")
    script.src = "https://www.redditstatic.com/comment-embed.js"
    script.async = true
    document.body.appendChild(script)
  }

  mountFacebook = () => {
    const script = document.createElement("script")
    script.src =
      "https://connect.facebook.net/en_US/sdk.js#xfbml=1&amp;version=v2.5"
    script.async = true
    document.body.appendChild(script)
  }

  mountAddThis = () => {
    const script = document.createElement("script")
    script.src =
      "//s7.addthis.com/js/300/addthis_widget.js#pubid=ra-5abeca193397ebe6"
    script.async = true
    document.body.appendChild(script)
  }

  mountLikes = () => {
    const script = document.createElement("script")
    script.innerHTML = "(function(d,e,s){if(d.getElementById(`likebtn_wjs`))return;a=d.createElement(e);m=d.getElementsByTagName(e)[0];a.async=1;a.id=`likebtn_wjs`;a.src=s;m.parentNode.insertBefore(a, m)})(document,`script`,`//w.likebtn.com/js/w/widget.js`);"
    script.async = true
    document.body.appendChild(script)
  }


  render() {
    const { disqus, post, shareUrl, title, page, article } = this.state

    return (
      <ThemeProvider theme={theme}>
        <BlogPost>
          <SEO page={page} article={article} />
          <div id="fb-root" />
          
          <ImageStyled featuredImage={post.frontmatter.featuredImage} />
          <PostHeader post={post} />
          
          <CasualText
            id="text"
            className="inner-text"
            dangerouslySetInnerHTML={{ __html: post.html }}
          />
         
         <span className="likebtn-wrapper" data-theme="custom" 
         data-icon_size="30" data-icon_l="hrt6" data-icon_d="hrt12" 
         data-icon_l_c="#000000" data-icon_l_c_v="#f50057" 
         data-icon_d_c="#000000" data-icon_d_c_v="#f50057" 
         data-label_c="#000000" data-label_c_v="#f50057" data-bg_c="#ffffff" 
         data-bg_c_v="#ffffff" data-brdr_c="#ffffff" data-ef_voting="heartbeat" 
         data-rich_snippet="true" data-dislike_enabled="false" data-icon_
         dislike_show="false" data-counter_clickable="true" data-counter_count="true" 
         data-lazy_load="true" data-loader_show="true" data-site_id="https://victoriaz.netlify.com"></span>

           <div className="addthis_relatedposts_inline"></div>
           
          <ReactDisqusThread
            shortname={disqus}
            identifier={title}
            title={title}
            url={"https://" + shareUrl}
          />

        </BlogPost>
      </ThemeProvider>
    )
  }
}

export const pageQuery = graphql`
  query PostByPath($path: String!) {
    site {
      siteMetadata {
        disqusShortname
        siteUrlShort
        siteUrl
        siteTitle
        siteLogo
      }
    }
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      shortExcerpt: excerpt(pruneLength: 70)
      longExcerpt: excerpt(pruneLength: 400)
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        tags
        featuredImage {
          name
          childImageSharp {
            sizes(maxWidth: 1900) {
              src
              srcSet
              ...GatsbyImageSharpSizes
            }
          }
        }
      }
    }
  }
`



